.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  box-sizing: border-box;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 30px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chessboard-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  max-width: 400px;
  position: relative;
}

.chessboard-container > div {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.jogador-nome {
  font-size: 0.85em;
  padding: 3px 0;
  color: #666;
  text-align: left;
  margin: 2px 0;
  opacity: 0.8;
  margin-left: 15px;
}

.jogador-nome.brancas {
  color: #666;
}

.jogador-nome.pretas {
  color: #666;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  padding: 8px;
  padding-top: 0;
  max-width: 400px;
  margin: 0 auto;
}

.feedback-container {
  flex: 1;
  min-width: 0;
}

.feedback {
  font-size: 14px;
  padding: 0 8px;
  border-radius: 4px;
  margin: 0;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 0;
  box-sizing: border-box;
}

.feedback p {
  margin: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1;
  text-align: center;
  width: 100%;
  font-size: 14px;
  font-weight: normal;
}

.feedback.error {
  background-color: rgba(255, 0, 0, 0.1);
  color: #ff0000;
}

.feedback.success {
  background-color: rgba(0, 255, 0, 0.1);
  color: #00aa00;
}

.feedback.info {
  background-color: rgba(0, 0, 0, 0.05);
  color: #333;
  margin: 0;
  padding: 0 8px;
}

.buttons-container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.control-button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: #f0f0f0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  padding: 0;
}

.control-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.control-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.control-button.error-button {
  background-color: rgba(255, 0, 0, 0.1);
  color: #ff0000;
}

.control-button.error-button:hover:not(:disabled) {
  background-color: rgba(255, 0, 0, 0.2);
}

.control-button.error-button svg {
  color: #ff0000;
}

.control-button svg {
  width: 24px;
  height: 24px;
}

.info {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 10px;

}

.info p {
  margin: 0;
  font-size: 1.1em;
  color: #333;
}

.info .tema {
  font-weight: bold;
  font-size: 1.2em;
}

.info .exercicio {
  color: #666;
  font-style: italic;
  margin: 5px 0;
}

.feedback-success {
  color: #2e7d32;
  font-weight: bold;
}

.feedback-error {
  color: #c62828;
  font-weight: bold;
}

.try-again {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  transition: background-color 0.2s;
  min-width: 120px;
}

.try-again:hover {
  background-color: #d32f2f;
}

